<template>
    <div class="page">
        <!-- search-section-->
        <div class="search-section">
            <div class="search-icon" @click="goPage()"></div>
            <div class="search-text" @click="$router.push('/goods/search')">请输入商品名称</div>
        </div>
        <!--goods-section-->
        <div class="goods-section">
            <div ref="goods-classify" class="goods-classify">
                <div>
                    <div ref="item" :class="{'classify-item':true, active:item.active}"
                         v-for="(item,index) in classifyData"
                         :key="index" @click="goClassifyItem('/goods/classify/item?cid='+item.cid,index)">
                        {{item.title}}
                    </div>
                </div>

            </div>
            <div class="goods-content">
            <router-view/>
            </div>
        </div>
    </div>
</template>

<script>
    import {mapState,mapActions,mapMutations} from "vuex"
    import IScroll from '../../assets/js/libs/iscroll';
    export default {

        computed:{
            //vuexmapstate
            ...mapState({
                //左边导航分类数据
                classifyData:(state)=>state.goods.classifyData
            })
        },
        methods:{

            ...mapMutations({
                //改变左边导航样式方法
                SELECT_ACTIVEITEM:"goods/SELECT_ACTIVEITEM"
            }),
            //禁用默认事件
            eventTouchmoveTop(e){
                e.preventDefault();
            },
            //获取左边导航分类vuex管理的数据
            ...mapActions({
                getClassify:"goods/getClassify"
            }),
            //classifyitem动态路由跳转
            goClassifyItem(url,index){
                this.$router.replace(url);
                this.ClassifyStyle(index);
            },
            //样式改变 以及滑动动画控制事件
            ClassifyStyle(index){
                //获取滑动当前高度
                let topHeight=this.$refs['item'][0].offsetHeight*index?this.$refs['item'][0].offsetHeight*index:'';
                //获取滑动当前高度3/1
                let halfHeight=parseInt(this.$refs['goods-classify'].offsetHeight/3);
                //总高度减去当前的高度
                let bottomHeight=parseInt(this.$refs['goods-classify'].scrollHeight-topHeight);
                if(topHeight>halfHeight && bottomHeight>this.$refs['goods-classify'].offsetHeight){
                    this.myScroll.scrollTo(0,-topHeight,1000,IScroll.utils.ease.elastic);
                }
                ////改变左边导航样式方法
                this.SELECT_ACTIVEITEM({index:index})
            },
            //路由跳转
            goPage(){
                this.$router.go(-1)
            }
        },

        created(){
            document.title =this.$route.meta.title;
            //获取cid路由参数 判断是否为空 否则会出现undefind
            this.cid = this.$route.query.cid?this.$route.query.cid:"";
            //运行导航分类方法
            this.getClassify({success:()=>{
                this.$nextTick(()=>{
                    //判断是否有数据 和cid是否存在  否则会出 undefind错误
                    if (this.classifyData.length>0 && this.cid){
                        let i = 0;
                        //获取当前数据的索引值
                        for (;i<this.classifyData.length;i++){
                            //当数据中的cid 等于路由的cid的时候 一直跳出循环 跳出当前的索引值， 0 1 2 3
                            if(this.classifyData[i].cid === this.cid){
                                break;
                            }
                        }
                        //然后赋值给样式改变的方法
                        this.ClassifyStyle(i);
                    }else{
                        //默认第一个
                        this.ClassifyStyle(0);
                    }
                    //样式滑动方法重计算
                    this.myScroll.refresh();
                })
                }})
        },

        mounted(){
            //goods-classify 滑动事件
            this.$refs["goods-classify"].addEventListener('touchmove', this.eventTouchmoveTop);
             this.myScroll= new IScroll(this.$refs["goods-classify"], {
                // eventPassthrough : true,
                scrollX : false,
                scrollY : true,
                preventDefault : false
                //click:true
            });
        },

        beforeDestroy(){
            //离开页面卸载监听事件
            this.$refs["goods-classify"].removeEventListener("touchmove",this.eventTouchmoveTop);
        }
    }
</script>

<style scoped>
.page {
    width: 100%;
    height: 100vh;
    overflow: hidden;
}
.goods-content {
    width: 71%;
    height: 100%;
}
/*search-section*/
.search-section {
    display: flex;
    align-items: center;
    width: 100%;
    height: 1rem;
    background: #FFF;
    border-bottom: 1px solid #efefef;
}
.search-section .search-icon {
    width: .8rem;
    height: .8rem;
    background: url("../../assets/images/home/goods/back.png") no-repeat;
    background-size: 100%;
    backgorund-position: center;
}
.search-section .search-text {
    box-sizing: border-box;
    width: 80%;
    height: .69rem;
    border: 1px solid #b2b2b2;
    border-radius: .1rem;
    font-size: .28rem;
    color: #626262;
    line-height: .69rem;
    padding-left: .2rem;
}
    /*goods-section*/
.goods-section {
    display: flex;
    width: 100%;
    height: 92.5vh;
}
.goods-section .goods-classify {
    width: 1.72rem;
    height: 100%;
    overflow: hidden;
    margin-right: 3%;
    position: relative;
    z-index: 1;
}
.goods-section .goods-classify .classify-item {
    width: 100%;
    height: .8rem;
    border-bottom: 1px solid #efefef;
    background-color: #fff;
    font-size: .28rem;
    text-align: center;
    line-height: .8rem;
    overflow: hidden;
}
.goods-section .goods-classify .active {
    color: red;
}

</style>